html {
  font-size: 62.5%;
}

* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-size: 1.6rem;
  font-family: "Nanum Gothic", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

/* Common  */

h1 {
  font-size: 1.8rem;
  padding: 1rem 0;
}

h2 {
  font-size: 1.6rem;
  padding: 1rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

a:hover {
  color: #ff8000;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

li {
  margin-top: 1rem;
}

/* this is to make any div display flex */
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.row2 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
}
/* this is to center footer content */
.row.center {
  justify-content: center;
}

.row.top {
  align-items: flex-start;
  flex-direction: row;
}

.col-1 {
  flex: 1 1 38rem;
}

.col-2 {
  flex: 1 1 60rem;
}

.min-30 {
  min-width: 30rem;
}

/* BUTTON/ */

input,
select,
textarea,
button {
  padding: 1rem;
  margin-top: 1rem;
  border: 0.1rem #a4a4a4 solid;
  border-radius: 1rem;
  display: block;
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
  cursor: pointer;
}

button {
  border-radius: 5rem;
}
input:hover,
select:hover,
textarea:hover,
button:hover {
  border: 0.1rem #404040 solid;
}

button.block {
  width: 100%;
}

button.primary {
  background-color: #f0c040;
}

/* IMAGE */

img {
  border-radius: 0.5rem;
}

img.small {
  border-radius: 0rem;
  max-width: 10rem;
  width: 100%;
}
img.medium {
  max-width: 29rem;
  width: 100%;
}

img.large {
  max-width: 60rem;
  width: 100%;
  /* height: 50rem; */
}

/* card */

.card {
  border: 0.1rem #c0c0c0 solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  margin: 1rem;
}

.card-body {
  padding: 1rem;
}

.card-body > * {
  margin-bottom: 0.5rem;
}

.price {
  font-size: 2rem;
  font-weight: 600;
}

/* ------------navbar-------- */

nav {
  line-height: 80px;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
}

/* //logo */
nav label {
  line-height: 8rem;
  font-size: 30px;
  font-weight: 200;
  margin-left: 8rem;
}

nav ul {
  float: right;
  margin-right: 8rem;
}

nav ul li {
  line-height: 8rem;
  display: inline-block;
  margin: 0 1rem;
  font-weight: 300;
  font-size: 20px;
}
nav ul li:hover {
  color: grey;
  transition: 0.5s;
}

.badge {
  background-color: gainsboro;
  color: #000;
  border-radius: 50%;
  padding: 0.2rem 0.7rem;
  margin-bottom: 0.5rem;
  font-size: 1.4rem;
  margin-left: 0.2rem;
}
/* setting menubars */
.menubar {
  float: right;
  padding-right: 5rem;
  display: none;
  cursor: pointer;
  /* background-color: white; */
}

.side_menu .outerul .fromhead {
  display: none;
}

/* //sidemenu or .leftmenu */

.side_menu {
  position: fixed;
  margin-top: 8.5rem;

  width: 25rem;
  height: 100%;
}

.side_menu .outerul {
  padding-left: 8rem;
}

.side_menu .outerul li {
  line-height: 25px;
  font-weight: 200;
}
.side_menu .innerul li {
  padding-left: 1.5rem;
}

.side_menu ul li:hover {
  padding-left: 5px;
  transition: 0.5s;
}

/* //data part  */
.data {
  min-height: 100vh;
  padding-left: 25rem;
  padding-top: 8.5rem;
  display: flex;
  flex-direction: column;
}

/* -----HOME PAGE CENTER BODY---------- */
.small img {
  width: 110px;
  height: 110px;
}
.mobliediv img {
  max-width: 50px;
  border-radius: 50%;
  display: none;
}

.mainposter {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-attachment: fixed;
}
.mainposter img {
  padding-left: 10px;
  padding-top: 10px;
  width: 50%;
}

.wrapper {
  margin-top: 20px;
  display: flex;
  overflow-x: auto;
  justify-content: space-between;
}

.wrapper::-webkit-scrollbar {
  width: 0%;
}

.wrapper div {
  min-width: 110px;
  height: 150px;
  text-align: center;
  margin-right: 2px;
}
.parallexbg {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 2rem;
  text-shadow: 2px 2px 2px #000;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 20px;
  width: 100%;
  height: 60vh;
  margin: 40px 0;
}
.parallexbg p {
  font-size: 8rem;
}

.typesnewcusto {
  margin-top: 2rem;
}

.typesnewcusto h3 {
  margin: auto;
  font-size: 3rem;
  text-align: center;
  font-weight: 500;
}

.row .item1 img {
  width: 35rem;
  height: 27rem;
}

/* .bottomnavmobile */
.bottomnavmobile {
  background-color: white;
  position: fixed;
  bottom: 0;
  width: 100%;
  line-height: 15px;
  padding: 0;
  right: -100%;
}
.bottomnavmobile .bttitem span {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
/* --------footer------------- */

footer {
  background-color: rgb(248, 248, 248);
  margin-top: auto;
  padding-top: 3rem;
}

/* comingsoon pagae */

/* alert */

.loading {
  display: block !important;
}
.success {
  color: #20a020;
}

.danger {
  color: #a02020;
}

.alert {
  padding: 1rem;
  border: 0.1rem solid transparent;
  border-radius: 0.5rem;
}

.alert-info {
  color: #2020a0;
  background-color: #e0e0ff;
}

.alert-danger {
  color: #a02020;
  background-color: #ffe0e0e0;
}

/* CARTSCREEN */
.itemincartlist {
  border: 1px solid #808080;
  padding: 2rem;
}
.centermeifnoitemincart {
  text-align: center;
}

/* FORM */

.form {
  max-width: 60rem;
  margin: 0 auto;
}

.form > div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.form label {
  margin: 1rem 0;
  display: block;
}

/* dropdown */
.dropdown {
  display: inline-block;
  position: relative;
}
.dropdown-content {
  position: absolute;
  display: none;
  left: 0;
  min-width: 12rem;
  padding: 1rem;
  z-index: 1;
  margin: 0;
  margin-top: 0.1rem;
  border-radius: 0.5rem;
}

.dropdown:hover .dropdown-content {
  display: block;
}

/* checkout steps */

.checkout-steps > div {
  border-top: 0.3rem #c0c0c0 solid;
  color: #c0c0c0;
  flex: 1;
  padding: 1rem;
  font-weight: bold;
}
.checkout-steps > div.active {
  border-top-color: #f08000;
  color: #f08000;
}

.menulines {
  display: none;
}

@media screen and (max-width: 650px) {
  .data {
    padding-left: 80px;
    padding-right: 80px;
  }

  nav ul {
    display: none;
  }
  nav .menubar {
    display: block;
  }

  .side_menu .outerul .fromhead {
    display: block;
  }
  .side_menu {
    width: 100%;
    padding-left: 0%;
    left: -100%;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.96);
    transition:  0.8s;
  }
  .side_menu.active {
    left: 0;
    top: -1rem;
  }

  .side_menu a:hover {
    text-decoration: underline;
  }
}

/* media query for home screen */
@media screen and (max-width: 650px) {
  nav label {
    margin-left: 0.5rem;
  }
  nav ul {
    margin-right: 0.5rem;
  }
  /* .menubar {
    padding-right: 10rem;
  } */
  .data {
    padding-left: 0%;
    padding-right: 0;
  }
  .mobliediv img {
    display: block;
  }

  .mainposter img {
    padding-left: 0;
    width: 100%;
  }

  .item1 {
    order: 1;
  }
  .item2 {
    order: 2;
  }
  .row .item1 img {
    width: 18.75rem;
    height: 27rem;
  }
  .typesnewcusto h3 {
    padding-top: 2rem;
    font-size: 1.8rem;
  }

  .parallexbg p {
    font-size: 4.5rem;
  }
  .bottomnavmobile {
    right: 0;
  }
}
